
.app-container {
    background-color: #98D7E4;
    padding-left: 50px;
}

nav {
    width: 100%;
    background-color: #FDFAB4;
    display: flex;
    justify-content: space-around;
    align-items: center;
    .nav-links {
        list-style: none;
        display: flex;
        .nav-icon a {
            position: relative;
            display: inline-block;
            text-decoration: none;
            color: rgb(0, 0, 0);
            padding: 1em 2em;
            &:hover {
                color: rgb(235, 101, 130);
            }
        }
    }
}

nav.vertical {
    width: 120px;
    height: 100vh;
    flex-direction: row;
    position: absolute;
    left: 0;
    top: 0;
    .nav-links {
        flex-direction: column;
        .nav-item a {
            display: block;
            padding: 4em 0;
            
        }
    }

}

.kanban {
	margin: 4em;
    &#home {
        text-align: center;
    }
	display: flex;
	padding: 30px;
	width: 750px;
	background: #009578;
	border-radius: 5px;
}

.kanban * {
	font-family: sans-serif;
}

.kanban__column {
	flex: 1;
}

.kanban__column:not(:last-child) {
	margin-right: 30px;
}

.kanban__column-title {
	margin-bottom: 20px;
	font-size: 30px;
	color: white;
	user-select: none;
}

.kanban__item-input {
	padding: 10px 15px;
	box-sizing: border-box;
	background: white;
	border-radius: 5px;
	cursor: pointer;
}

.kanban__dropzone {
	height: 10px;
	transition: background 0.15s, height 0.15s;
}

.kanban__dropzone--active {
	height: 20px;
	background: rgba(0, 0, 0, 0.25);
}

.kanban__add-item {
	width: 100%;
	padding: 10px 0;
	font-size: 16px;
	color: white;
	background: rgba(0, 0, 0, 0.1);
	border: none;
	border-radius: 5px;
	cursor: pointer;
}